import { get, post, del, patch } from './http/httpMethods'

function addLead(payload) {
    return post('/lead', payload).then((response) => {
        return response;
    }).catch((err) => console.log(err));
}

// add drafted leads
function publishLeads(payload) {
    return patch('/lead', payload).then((response) => {
        return response;
    }).catch((err) => console.log(err));
}

function leadUpload(payload) {
    return post('/lead/lead-upload', payload).then((response) => {
        return response;
    }).catch((err) => console.log(err));
}

function updateLead(leadId, payload) {
    return patch(`/lead/${leadId}`, payload).then((response) => {
        return response;
    }).catch((err) => console.log(err));
}

function getLeads(query) {
    const queryString = Object.entries(query)
        .map(([key, value]) => `${encodeURIComponent(key)}=${encodeURIComponent(value)}`)
        .join('&');
    const url = 'lead' + '?' + queryString;
    return get(url).then((response) => {
        return response;
    })
}

function deleteLead(leadId) {
    return del(`/lead/${leadId}`).then((response) => {
        console.log(response);
        return response;
    })
}

function getLead(leadId) {
    return get(`/lead/${leadId}`).then((response) => {
        return response;
    })
}

function getBuyerLeads(query) {
    const queryString = Object.entries(query)
        .map(([key, value]) => `${encodeURIComponent(key)}=${encodeURIComponent(value)}`)
        .join('&');
    const url = 'lead/list' + '?' + queryString;

    return get(url).then((response) => {
        return response;
    })
}

function getDraftedLeads(query) {
    const queryString = Object.entries(query)
        .map(([key, value]) => `${encodeURIComponent(key)}=${encodeURIComponent(value)}`)
        .join('&');

    const url = 'lead/list' + '?' + queryString;
    return get(url).then((response) => {
        return response;
    })
}

export const leadService = {
    addLead,
    getLeads,
    getBuyerLeads,
    deleteLead,
    getLead,
    updateLead,
    leadUpload,
    getDraftedLeads,
    publishLeads
}