import { useState, useEffect } from 'react';
import { Grid, Typography, Box, FormControl, InputAdornment, OutlinedInput } from '@mui/material';

// assets
import { SearchOutlined } from '@ant-design/icons';

// services
import { leadService } from '../../services/leadService'


// ==============================|| DASHBOARD - DEFAULT ||============================== //

const DashboardDefault = () => {
  const [list, setList] = useState([])

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await leadService.getLeads({draft:false});
        setList(response);
      } catch (error) {
        // Handle error
      }
    };

    fetchData();
  }, []);

  // search function
  const handleChange = async (arg) => {
    const response = await leadService.getLeads({draft:false,search:arg});
    setList(response)
  }

  function debounce(cb, delay) {
    let timer;
    return function (...args) {
      clearTimeout(timer)
      timer = setTimeout(() => cb(...args), delay)
    }
  }
  let handle = debounce(handleChange, 500)

  return (
    <Grid container rowSpacing={4.5} columnSpacing={2.75}>
      <Grid item xs={12} sx={{ mb: -2.25 }}>
        <Grid container sx={{ mb: 2}}>
          <Grid item xs={3} sx={{mb:2, display: 'flex', alignItems: 'center' }} >
            <Typography variant='h5'>
              Search For Leads
            </Typography>
          </Grid>
          <Grid item xs={9}>
            <Box >
              <FormControl fullWidth >
                <OutlinedInput
                  size="small"
                  id="header-search"
                  startAdornment={
                    <InputAdornment position="start" >
                      <SearchOutlined
                      />
                    </InputAdornment>
                  }
                  onChange={(e) => handle(e.target.value)}
                  sx={{backgroundColor: 'rgba(255, 255, 255)' }}
                  aria-describedby="header-search-text"
                  inputProps={{
                    'aria-label': 'weight'
                  }}
                  placeholder="Ctrl + K"
                />
              </FormControl>
            </Box>
          </Grid>
        </Grid>
        {
          list?.map((item) => {
            return (
              <Box sx={{ border: '1px solid #ccc', padding: 2, borderRadius: 2, mb: 2, backgroundColor: 'rgba(255, 255, 255)' }}>
                <Grid container spacing={1} sx={{ pt: 0 }}>
                  <Grid item xs={2} sx={{ p: 0, h: 100 }}>
                    <Box >
                      <Typography>
                        <span style={{ fontWeight: 'normal' }}>Product:</span>
                      </Typography>
                      <Typography>
                        Quantity:
                      </Typography>
                      <Typography>
                        UAN Number:
                      </Typography>
                      <Typography>
                        Specification:
                      </Typography>
                      <Typography>
                        Note: {item.note}
                      </Typography>
                    </Box>
                  </Grid>
                  <Grid item xs={10} sm={6} sx={{ p: 0, h: 100 }}>
                    <Box sx={{}}>
                      <Typography>
                        {item.product}
                      </Typography>
                      <Typography>
                        {item.quantity}
                      </Typography>
                      <Typography>
                         {item.uanNumber}
                      </Typography>
                      <Typography>
                      {item.specification}
                      </Typography>
                    </Box>
                  </Grid>
                  <Grid item sm={4} sx={{ p: 0, h: 100 }}>
                    <Box sx={{ p: 3, width: '100%', height: "100%" }}>
                      <Typography>
                        buyer: {item.buyer?.name}
                      </Typography>
                      <Typography>
                        email: {item.buyer?.email}
                      </Typography>
                      <Typography>
                        phone: {item.buyer?.phone}
                      </Typography>
                    </Box>
                  </Grid>
                </Grid>
              </Box>)
          }
          )
        }
      </Grid>
    </Grid>
  );
};

export default DashboardDefault;
